	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/07/crhinside.jpg\" height=\"383\" width=\"300\" border=\"0\" alt=\"crhinside.jpg: \" /></p>\n<p>Some days you never know what you'll find in the kitchen cupboards.</p>\n<p><a href=\"http://www.spiegel.de\"><img src=\"http://www.spiegel.de/img/0,1020,119329,00.jpg\" align=\"left\" border=\"0\" /></a>This week's <a href=\"http://www.spiegel.de\">Spiegel</a> has a cover story on 'The new mother's pride: children instead of career' that Mama found amusing. In western Germany just 5% of the mothers with pre-kindergarten children work full time, and just 16% of mothers of children in elementary school. And how many mothers are responsible for tens of millions of monthly revenue and several thousand employees? I personally found the <a href=\"http://www.spiegel.de/spiegel/0,1518,145177,00.html\">story</a> about <a href=\"http://www.msco.org\">Sherrif Joe</a> from Phoenix and his tent jails and chain gangs entertaining. I don't think I'd eat his green bologna, though.</p>","fields":{"title":"Christopher inside","date":"19 Jul 2001","url":"https://papascott.de/2001-07-19-christopher-inside/"}}},"pathContext":{"slug":"/2001-07-19-christopher-inside/","prev":{"fields":{"title":"Herding cats","date":"17 Jul 2001","slug":"/2001-07-17-herding-cats/"}},"next":{"fields":{"title":"Drunk and disorderly","date":"20 Jul 2001","slug":"/2001-07-20-drunk-and-disorderly/"}}}}

